import { FormGroup } from '@angular/forms';
import { PaymentHooksOptions } from '../../configs/models/form-items.model';
import { PaymentHooks } from '../../configs/models/form-item-names.model';

export function triggerPaymentHook(
  hooks: PaymentHooksOptions,
  hookName: PaymentHooks,
  callback?: () => void,
  formGroup?: FormGroup
) {
  const hook = hooks && hooks[hookName];
  const doneFn = callback as () => void;

  if (typeof hook === 'function') {
    if (
      hookName === PaymentHooks.onSave ||
      hookName === PaymentHooks.onSubmit
    ) {
      hook({
        doneFn, // Callback function that is triggered when hooks complete
        form: formGroup as FormGroup, // The entire payment form
      });
    } else {
      hook();
    }
  }
}
